<template>
	<div class="textarea">
		<div
			ref="input"
			@click="clickInput"
			@blur="onBlur"
			:contenteditable="mode=='edit'?true:false"
			class="input"
			:class="{'left':align==='left','center':align==='center','right':align==='right','mode-edit':mode=='edit'}"
			@input="inputEvent"
		></div>
		<div class="placeholder" :class="{'left':align==='left','center':align==='center','right':align==='right',}" v-if="!input">{{placeholder}}</div>
	</div>
</template>
<script>
import device from 'current-device'
/*
:disabled="mode=='edit'?true:false"
*/
function keepLastIndex(obj) {
	/* 	console.log(obj)
	console.log(window.getSelection)
	console.log(document.selection) */
	if (window.getSelection) {
		//ie11 10 9 ff safari
		obj.focus() //ff
		var range = window.getSelection() //range
		range.selectAllChildren(obj) //range obj
		range.collapseToEnd() //
	} else if (document.selection) {
		//ie10 9 8 7 6 5
		var range = document.selection.createRange() //
		//var range = document.body.createTextRange();
		range.moveToElementText(obj) //rangeobj
		range.collapse(false) //
		range.select()
	}
}

export default {
	props: {
		placeholder: {
			default: 'TEXT'
		},
		align: {
			default: 'left' //left center right
		},
		mode: {
			default: ''
		}
	},
	computed: {},
	watch: {},
	data() {
		return {
			input: ''
		}
	},
	methods: {
		inputEvent() {
			let _this = this
			setTimeout(() => {
				keepLastIndex(_this.$refs.input)
			}, 5)
			let val = this.$refs.input.innerText.trim()
			this.input = val
			this.$emit('eInput', val)
		},
		getVal() {
			return {
				text: this.input
			}
		},
		setVal(text) {
			this.$refs.input.innerText = text.trim()
			this.input = text
		},
		clickInput(target) {
			if (!device.desktop()) {
				target.target.scrollIntoView()
			}
		},
		onBlur() {
			if (!device.desktop()) {
				let ele = document.getElementById('app')
				setTimeout(() => {
					ele.scrollTop = 0
					document.body.scrollTop = 0
				}, 300)
			}
		}
	},
	mounted() {}
}
</script>
