	width: 100%;
	position: relative;
	box-sizing: border-box;
	.input {
		position: relative;
		z-index: 2;
		outline: none;
		box-sizing: border-box;
		word-break: break-all;
		background: transparent;
		&.left {
			text-align: left;
		}
		&.center {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: center;
		}
		&.right {
			text-align: right;
		}

		&.mode-edit {
			-webkit-user-modify: read-write-plaintext-only;
		}
	}
	.placeholder {
		width: 100%;
		position: absolute;
		top: 0;
		left: 0px;
		color: rgba(153, 153, 153, 1);
		z-index: 0;
		&.left {
			text-align: left;
		}
		&.center {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: center;
		}
		&.right {
			text-align: right;
		}
	}
}
</style>
